.form{
  &-test{
    &__wrapper{
      max-width: 850px;
      margin: 0 auto;
    }
    &__blocks{
      margin: 40px 0 0 0;

      min-height: 316px;
      &-big{
        margin: 56px 0 0 0;
      }
    }
    &__description{
      margin: 32px 0 0 0;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;

      color: #2F2F2F;
      &-small{
        font-size: 14px;
        line-height: 17px;

        a{
          font-weight: 700;
        }
      }
      & + &{
        margin: 7px 0 0 0;
      }
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 16px;
      &-block{
        width: 100%;
      }
      &-description{
        margin: 24px 0 0 0;
      }
      &-footer{
        margin: 56px 0 0 0;
      }
    }
    &__checkbox{
      width: 100%;
      display: block;
      &:hover{
        cursor: pointer;
      }
      &-item{
        display: none;
        &:checked ~ .form-test__checkbox-checkmark{
          &::before{
            opacity: 1;
          }

          background: #6776FF;
          border-color: #6776FF;
        }
        &:checked ~ .form-test__checkbox-bg{
          opacity: 1;
        }
      }
      &-bg{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        background: #F9F9FF;

        z-index: 0;

        opacity: 0;

        border-radius: 16px;

        transition: .3s opacity;
      }
      &-wrapper{
        padding: 24px;
        width: 100%;
        display: flex;
        align-items: center;

        position: relative;

        border: 1px solid #6776FF;
        box-shadow: 0px 6px 18px rgba(0, 10, 105, 0.08);
        border-radius: 16px;

        overflow: hidden;
      }
      &-checkmark{
        width: 22px;
        height: 22px;
        display: block;

        flex-shrink: 0;

        background: #F9F9FF;
        border: 1px solid #C0C6FF;
        border-radius: 2px;

        margin: 0 16px 0 0;

        position: relative;

        z-index: 2;

        transition: .3s background-position;

        &-radio{
          border-radius: 50%;
          background: transparent;
        }

        &::before{
          content: '';
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;

          display: block;

          background: url("../img/check.svg") center center/contain no-repeat;

          opacity: 0;

          transition: .3s opacity;
        }
      }
      &-label{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #000000;

        position: relative;
        z-index: 2;
      }
    }
    &__button{
      max-width: 420px;
      width: 100%;

      margin: 0 auto;
    }
    &__info{
      margin: 32px 0 0 0;
      &-finish{
        margin: 150px 0 0 0;
      }
      &-block{
        &-center{
          display: flex;
          align-items: center;
        }
      }
      &-wrapper{
        padding: 24px 48px;
        display: grid;
        grid-template-columns: 64px auto 84px;
        gap: 84px;

        background: #F0F0F0;
        border-radius: 20px;
      }
      &-progress{
        position: relative;
        height: 16px;
        width: 100%;

        background: #FFFFFF;
        border-radius: 100px;
      }
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        color: #2F2F2F;
        &-right{
          text-align: right;
        }
      }
      &-title{
        margin: 8px 0 0 0;
      }
    }
    &__footer{
      margin: 42px 0 0 0;
    }
    &__img{
      width: 100%;
      height: 100%;

      &-item{
        width: 100%;
        height: 100%;

        border-radius: 10px;

        object-fit: cover;
      }
    }
    &__list{
      &-item{
        & + &{
          margin: 16px 0 0 0;
        }
      }
    }
    &__tips{
      display: flex;
      align-items: flex-start;
      &::before{
        content: '';
        width: 18px;
        height: 26px;
        display: block;

        background: url("../img/tips.svg") center center/contain no-repeat;

        margin: 0 16px 0 0;

        flex-shrink: 0;
      }
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;
      }
    }
    &__upload{
      &-input{
        display: none;
      }
      &-checkmark{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 240px;
        height: 50px;

        margin: 0 auto 20px auto;

        font-family: 'Gilroy', sans-serif;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;

        color: #5465FF;

        background: #FFFFFF;
        box-shadow: 0px 2px 6px rgba(0, 35, 103, 0.05);
        border-radius: 40px;
        &::after{
          content: '';
          width: 17px;
          height: 17px;
          display: block;

          background: url("../img/download-2.svg") center center/contain no-repeat;

          margin: 0 0 0 10px;
        }
      }
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #2F2F2F;
        display: block;
        & + &{
          margin: 8px 0 0 0;
        }
        &-grey{
          color: #B6B6B6;
        }
      }
      &-wrapper{
        padding: 24px 24px 16px 24px;
        display: block;

        background: #F9F9FF;

        border: 1px dashed #6776FF;
        border-radius: 12px;
      }
    }
  }
}

.test{
  &-result{
    position: relative;
    z-index: 3;
    &__wrapper{
      max-width: 850px;

      margin: 0 auto;
    }
    &__content{
      margin: 16px 0 0 0;
    }
    &__info{
      padding: 24px 48px;

      background: #F0F0F0;
      border-radius: 20px;

      margin: 40px 0 0 0;
      &-item{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
      }
      &-title{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        color: #2F2F2F;

        text-align: center;
      }
      &-circle{
        margin: 16px 0 0 0 ;
      }
    }
    &__grid{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }
    &__buttons{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      margin: 40px 0 0 0;
      &-item{
        & + &{
          margin: 16px 0 0 0;
        }
      }
    }
    &__answers{
      margin: 96px 0 0 0;
      &-content{
        margin: 40px 0 0 0;
      }
    }
  }
  &-answers{
    &__filter{
      display: flex;
      align-items: center;
      &-item{
        display: flex;
        align-items: center;

        &:hover{
          cursor: pointer;
        }

        & + &{
          margin: 0 0 0 40px;
        }
        &.active{
          .test-answers__filter-checkbox{
            border-color: #6776FF;
            background: #6776FF;
            &::before{
              opacity: 1;
            }
          }
        }
      }
      &-checkbox{
        width: 22px;
        height: 22px;
        display: block;

        border: 1px solid #C0C6FF;
        border-radius: 2px;

        position: relative;

        transition: 0.3s background-color, .3s border-color;
        &::before{
          content: '';
          position: absolute;
          left: 0;
          top: 0;

          width: 100%;
          height: 100%;
          display: block;

          opacity: 0;

          background: url("../img/check.svg") center center/contain no-repeat;

          transition: .3s opacity;
        }
      }
      &-text{
        margin: 0 0 0 10px;

        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #000000;
      }
    }
    &__content{
      margin: 16px 0 0 0;
      &-item{
        & + &{
          margin: 16px 0 0 0;
        }
      }
    }
    &__item{
      &[data-status='wrong']{
        .test-answers__item-content{
          &::before{
            background: url("../img/wrong-answer.svg") center center/contain no-repeat;
          }
        }
        .test-answers__item-block_tips{
          display: block;
        }
      }
      &-wrapper{
        padding: 24px 32px;
        border: 1px solid #6776FF;
        border-radius: 20px;
      }
      &-content{
        margin: 24px 0 0 0;
        display: flex;
        align-items: flex-start;
        &::before{
          content: '';
          width: 20px;
          height: 20px;
          display: block;

          flex-shrink: 0;

          background: url("../img/correct-answer.svg") center center/contain no-repeat;

          margin: 0 16px 0 0;
        }
      }
      &-block{
        & + &{
          margin: 16px 0 0 0 ;
        }
        &_tips{
          display: none;
        }
      }
      &-title{
        font-family: 'Gilroy', sans-serif;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        color: #2F2F2F;
      }
      &-title + &-text{
        margin: 8px 0 0 0;
      }
      &-title + &-text-small{
        margin: 4px 0 0 0;
      }
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #2F2F2F;
        &-small{
          font-size: 16px;
          line-height: 19px;
        }
        a{
          color: #6776FF;
          font-size: 14px;
          text-decoration: underline;
        }
      }
      &-media{
        margin: 16px 0 0 0;

        width: 185px;
        height: 140px;

        img,video{
          width: 100%;
          height: 100%;

          object-fit: cover;

          border-radius: 10px;
        }
      }
    }
  }
}

.circle{
  &-test{
    &__wrapper{
      width: 150px;
      height: 150px;

      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
    }
    &__item{
      position: absolute;
      left: 0;
      top: 0;
      z-index: 2;

      width: 100%;
      height: 100%;

      background: #FFFFFF;

      border-radius: 50%;
    }
    &__text{
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 31px;
      line-height: 38px;

      color: #2F2F2F;

      position: relative;
      z-index: 3;
    }
  }
}

@media screen and (max-width: 767px) {
  .form{
    &-test{
      &__grid{
        grid-template-columns: minmax(0,1fr);
        &-description{
          margin: 16px 0 0 0 ;
        }
        &-footer{
          margin: 24px 0 0 0;
        }
      }
      &__blocks{
        margin: 24px 0 0 0;
      }
      &__text{
        font-size: 14px;
        line-height: 17px;
      }
      &__description{
        margin: 16px 0 0 0;
      }
      &__list{
        &-item{
          & + &{
            margin: 8px 0 0 0;
          }
        }
      }
      &__footer{
        margin: 32px 0 0 0;
      }
      &__info{
        margin: 64px 0 0 0;
        &-wrapper{
          grid-template-columns: repeat(2,minmax(0,1fr));
          gap: 16px;

          padding: 12px 16px;
        }
        &-block{
          order: 1;
          &-center{
            grid-column-end: span 2;
            order: 2;
          }
        }
      }
      &__upload{
        &-text{
          display: none;
          &-grey{
            display: block;
          }
        }
      }
    }
  }
  .test{
    &-result{
      &__info{
        padding: 24px 48px;
        &-circle{
          margin: 12px 0 0 0;
        }
      }
      &__grid{
        flex-direction: column;
        align-items: center;
        &-item{
          & +& {
            margin: 24px 0 0 0;
          }
        }
      }
    }
    &-answers{
      &__item{
        &-wrapper{
          padding: 24px 16px;
        }
      }
    }
  }
}