.popup{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;


  z-index: -1;
  background: rgba(#000,.45);


  opacity: 0;

  transition: .5s all linear;
  &.active{
    opacity: 1;
    z-index: 20;
  }
  &-login{
    &__content{
      margin: 11vh auto 0 auto;
      max-width: 635px;
      width: 100%;
      background: #FFFFFF;
      box-shadow: 0px 0px 10px rgba(7, 14, 77, 0.1);
      border-radius: 20px;

      padding: 56px 24px 64px 24px;

      position: relative;
    }
    &__desc{
      margin: 8px 0 0 0;


      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #2F2F2F;
      &-center{
        text-align: center;
      }
    }
    &__wrapper{
      max-width: 315px;
      width: 100%;

      margin: 0 auto;
      &-big{
        max-width: 365px;
      }
    }
    &__close{
      position: absolute;
      right: 24px;
      top: 24px;
    }
  }
}

.login{
  &-form{
    &__checkbox{
      & + &{
        margin: 12px 0 0 0;
      }
    }
    &__content{
      margin: 24px 0 0 0;
      &-small{
        margin: 16px 0 0 0;
      }
    }
    &__input{
      & +& {
         margin: 16px 0 0 0;
      }
    }
    &__footer{
      margin: 32px 0 0 0;
      &-big{
        margin: 40px 0 0 0;
      }
    }
    &__register{
      margin: 24px 0 0 0;
    }
    &__img{
      width: 100%;
      height: 210px;

      display: flex;
      align-items: center;
      justify-content: center;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #000000;

      & + &{
        margin: 8px 0 0 0;
      }
    }
    &__controls{
      margin: 8px 0 0 0;

      display: flex;
      align-items: center;
      justify-content: center;
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;

        color: #2F2F2F;
      }
    }
    &__pagination{
      position: relative;
      width: 120px;
      height: 7px;
      display: block;

      margin: 0 0 0 8px;
      .swiper-pagination{
        background: #F1F1F1;
        border-radius: 40px;
        overflow: hidden;
        &-progressbar-fill{
          background: #FFA319;
          border-radius: 40px;
        }
      }
    }
    &__back{
      position: absolute;
      left: 24px;
      top: 24px;

      width: 32px;
      height: 32px;
      display: block;

      background: url("../img/back-popup.svg") center center/contain no-repeat;
    }
  }
}

.popup{
  &-close{
    &:hover{
      cursor: pointer;
    }
    &::before{
      content: '';
      width: 32px;
      height: 32px;
      display: block;

      background: url("../img/close-popup.svg") center center/contain no-repeat;
    }
  }
}

@media screen and (max-width: 767px) {
  .login{
    &-form{
      &__footer{
        &-big{
          margin: 32px 0 0 0;
        }
      }
      &__back{
        top: 12px;
        left: 16px;
      }
    }
  }
  .popup{
    &-login{
      &__close{
        top: 12px;
        right: 16px;
      }
    }
  }
}