.profile{
  &-block{
    &__wrapper{
      max-width: 850px;

      background: #F8F8F8;
      border-radius: 25px;

      padding: 32px 110px;

      margin: 0 auto;
    }
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 24px 150px;
      &-item{
        &_row{
          &-big{
            grid-row-end: span 2;
          }
        }
      }
    }
    &__title{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;

      color: #2F2F2F;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 600;
      font-size: 16px;
      line-height: 16px;

      color: #2F2F2F;
      a{
        margin: 0 0 0 24px;
      }
    }
    &__title + &__text{
      margin: 4px 0 0 0;
    }
    &__link{
      width: 240px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      padding: 12px 16px;

      background: #FFFFFF;
      border-radius: 10px;

      margin: 12px 0 0 0;
      &-text{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;

        max-width: 170px;
        width: 100%;

        overflow: hidden;

        text-overflow:ellipsis;
      }
      &-item{
        display: block;
        width: 22px;
        height: 22px;

        flex-shrink: 0;

        margin: 0 0 0 20px;
      }
    }
  }
  &-checkbox{
    display: flex;
    align-items: center;
    &__input{
      display: none;
      &:checked ~ .profile-checkbox__checkmark{
        background: #46C35A;
        justify-content: flex-end;
        &::before{
          left: 50%;
        }
      }
    }
    &__label{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #2F2F2F;
    }
    &__checkmark{
      flex-shrink: 0;

      width: 40px;
      height: 22px;
      background: #C7C7C7;
      box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.1);
      border-radius: 14.5px;

      margin: 0 0 0 14px;

      padding: 2px;

      transition: .3s background-color linear;

      position: relative;
      &::before{
        content: '';
        position: absolute;
        left: 2px;
        top: 2px;
        width: 18px;
        height: 18px;
        display: block;

        background: #FFFFFF;
        box-shadow: 1px 0px 4px rgba(0, 0, 0, 0.2);
        border-radius: 50%;

        transition: .3s all linear;
      }
    }
  }
  &-table{
    &__content{
      margin: 14px 0 0 0;
      background: #F8F8F8;
      border-radius: 15px;
    }
    &__row{
      display: grid;
      grid-template-columns: auto 100px 75px 116px 125px;
      gap: 120px;

      padding: 17px 32px;
      & + &{
        border-top: 1px solid #D7D7D7;
      }
      &-title{
        background: #FFA319;
        border-radius: 15px;
        height: 64px;

        padding: 0 32px;
      }
      &-block{
        display: flex;
        justify-content: flex-end;
        flex-direction: column;
        height: 100%;
        &-center{
          justify-content: center;
        }
      }
    }
    &__text{
      font-family: 'Gilroy', sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;

      color: #2F2F2F;

      &-grey{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;
      }
      & + &{
        margin: 24px 0 0 0;
      }
    }
  }
  &-link{
    display: flex;
    align-items: center;

    &::before{
      content: '';
      width: 17px;
      height: 17px;
      display: block;

      background: url("../img/download.svg") center center/contain no-repeat;

      margin: 0 8px 0 0;
    }
  }
}

@media screen and (max-width: 1100px) {
  .profile{
    &-table{
      &__row{
        gap: 30px;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .profile{
    &-block{
      &__grid{
        grid-template-columns: minmax(0,1fr);
        gap: 16px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .profile{
    &-table{
      &__row{
        &-block{
          display: none;
          &:nth-child(1),&:nth-child(2),&:nth-child(3){
            display: flex;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .profile{
    &-block{
      &__wrapper{
        padding: 24px 16px;
      }
    }
    &-table{
      &__row{
        gap: 0;
        grid-template-columns: auto 75px 55px;
        padding: 16px 12px;
        &-title{
          padding: 15px 12px;
        }
      }
      &__text{
        font-size: 12px;
        line-height: 15px;
      }
    }
  }
}