.form{
  &-input{
    width: 100%;
    position: relative;
    &__item{
      -webkit-appearance: none;
      outline: none;

      padding: 24px;

      width: 100%;
      height: 100%;

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      border: 1px solid #C0C6FF;
      border-radius: 12px;

      resize: none;

      &-small{
        height: 52px;

        padding: 17px;
      }
      &-promt{
        padding-top: 24px;
        padding-bottom: 12px;
      }
      &::placeholder{
        color: #929292;
        opacity: 1;
      }
      &:valid{
        background: #F9F9FF;
      }
      &:focus,&:valid{
         & +.form-input__label{
          font-size: 10px;
          top: 8px;
        }
      }
    }
    &__label{
      position: absolute;
      left: 17px;
      top: calc(50% - 8px);

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #929292;

      transition: .3s all;
    }
  }
}